import React, { useState, useEffect } from 'react';
import './Signup.css'; // Make sure this file exists and is in the same directory
import axios from 'axios'; // Import Axios
import { Link, useNavigate } from 'react-router-dom';
import { BASE_URL } from '../../App';



const Signup = ( {onLoginOrSignup}  ) => {
    const navigate = useNavigate();

    const [formData, setFormData] = useState({
        name: '',
        email: '',
        password: '',
        confirmPassword: '',
        phoneNumber: '',
    });
    const url = `${BASE_URL}/users/signup`;
    const [errors, setErrors] = useState({});
    const [typedText, setTypedText] = useState('');
    const [currentCharIndex, setCurrentCharIndex] = useState(0);
    const textToType = "Expense Tracker";

    useEffect(() => {
        const timer = setTimeout(() => {
        if (currentCharIndex < textToType.length) {
            setTypedText(prevTypedText => prevTypedText + textToType[currentCharIndex]);
            setCurrentCharIndex(prevIndex => prevIndex + 1);
        }
        }, 150); // Adjust the delay as needed

        return () => clearTimeout(timer);
    }, [currentCharIndex]);

    const validateForm = () => {
        let formIsValid = true;
        let newErrors = {};

        if (!formData.name) {
        formIsValid = false;
        newErrors['name'] = 'Name is required';
        }

        if (!formData.email) {
        formIsValid = false;
        newErrors['email'] = 'Email is required';
        } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
        formIsValid = false;
        newErrors['email'] = 'Email is not valid';
        }

        if (!formData.password) {
            formIsValid = false;
            newErrors['password'] = 'Password is required';
        }
        else if (formData.password.length < 8) {
            formIsValid = false;
            newErrors['password'] = 'Password must be at least 8 characters long';
        }
        else if (!/(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[!@#$%^&*])/.test(formData.password)) {
            formIsValid = false;
            newErrors['password'] = 'Password must contain at least one lowercase letter, one uppercase letter, one digit, and one special character';
        }
    

        if (formData.confirmPassword !== formData.password) {
        formIsValid = false;
        newErrors['confirmPassword'] = 'Passwords do not match';
        }

        if (!formData.phoneNumber) {
        formIsValid = false;
        newErrors['phoneNumber'] = 'Phone number is required';
        } else if (!/^\d{10}$/.test(formData.phoneNumber)) {
        formIsValid = false;
        newErrors['phoneNumber'] = 'Invalid phone number, must be 10 digits';
        }

        setErrors(newErrors);
        return formIsValid;
    };

    const handleSubmit = async (event) => {
        event.preventDefault();
        if (validateForm()) {
            axios.post(url, formData)
            .then(response => {
                if( response.data.success ){
                    const token = response.data.token;
                    localStorage.setItem('token', token);
                    localStorage.setItem('email', formData.email );
                    localStorage.setItem('userid', response.data.userid );
                    onLoginOrSignup(); // Call the onLoginOrSignup function passed from the App component
                    navigate('/homepage'); // Redirect to homepage after successful signup
                } else{
                    let newErrors = {};
                    newErrors['email'] = response.data.message;
                    setErrors(newErrors);
                }
            })
            .catch(error => {
                let newErrors = {};
                if (error.response && error.response.data) {
                    newErrors['email'] = error.response.data.message;
                } else {
                    newErrors['email'] = 'An error occurred during signup';
                }
                setErrors(newErrors);
            });
        }
    };

    const handleChange = (event) => {
        setFormData({ ...formData, [event.target.name]: event.target.value });
    };

    return (
        <div className="form-container bg-gray-dark-mktg">
            <div className="logo" role="heading" aria-level="1">
            <span>{typedText}</span>
            </div>
            <form className="signup-form" onSubmit={handleSubmit}>
            <div> Sign up </div>
                <hr/>
            <div className="form-group">
                <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleChange}
                placeholder="Name"
                className={errors.name ? 'error signup-form' : 'signup-form'}
                aria-label="Name"
                required
                />
                {errors.name && <p className="error-text">{errors.name}</p>}
            </div>

            <div className="form-group">
                <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                placeholder="Email"
                className={errors.email ? 'error signup-form' : 'signup-form'}
                aria-label="Email"
                required
                />
                {errors.email && <p className="error-text">{errors.email}</p>}
            </div>

            <div className="form-group">
                <input
                type="password"
                name="password"
                value={formData.password}
                onChange={handleChange}
                placeholder="Password"
                className={errors.password ? 'error signup-form' : 'signup-form'}
                aria-label="Password"
                required
                />
                {errors.password && <p className="error-text">{errors.password}</p>}
            </div>

            <div className="form-group">
                <input
                type="password"
                name="confirmPassword"
                value={formData.confirmPassword}
                onChange={handleChange}
                placeholder="Confirm Password"
                className={errors.confirmPassword ? 'error signup-form' : 'signup-form'}
                aria-label="Confirm Password"
                required
                />
                {errors.confirmPassword && <p className="error-text">{errors.confirmPassword}</p>}
            </div>

            <div className="form-group">
                <input
                type="tel"
                name="phoneNumber"
                value={formData.phoneNumber}
                onChange={handleChange}
                placeholder="Phone Number"
                className={errors.phoneNumber ? 'error signup-form' : 'signup-form'}
                aria-label="Phone Number"
                required
                />
                {errors.phoneNumber && <p className="error-text">{errors.phoneNumber}</p>}
            </div>

            <div className="form-group">
                <button type="submit" className="signup-button">Sign Up</button>
            </div>
            <div>
            Already have an account? <Link to="/login" role="menuitem">Login</Link>
            </div>
            </form>
        </div>
    );
};

export default Signup;